@font-face {
  font-family: font;
  src: url(fonts/hey.ttf);
}

body {
  margin: 20px 50px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to bottom right, #dd1173, #350e47);
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mint-container{

text-align: center;
margin: 20px 50px 450px 50px;
}

p{
  font-family: font;
  font-size: large;
  margin: 20px 50px;
}

h1{
  font-family: font;
  font-size: medium;
  margin: 20px 50px;
}

.connectbutton {
  color: white;
}
